
.sa-iop {
    width: 100%;
    height: 60vh;
    background: rgba(0, 0, 0, 0.342);
    position: relative;
  }
  
  .sa-iop:before {
    content: '';
    position: absolute;
    background: url('../../assets/programs_iop_bg.webp') no-repeat center center/cover;
    height: 100%;
    width: 100%;
    z-index: -1;
  }


  .sa-iop {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.sa-iop .sa-iop-content {
    display: block;
    position: absolute;
    bottom: 10%;
    padding: 1rem;
}


.sa-iop h1 {
    color: white;
    font-size: 3rem;
    text-align: center;
    font-weight: 300;
}

.sa-iop p {
    color: white;
    text-align: center;
    font-weight: 300;
}


/* MEDIA QUERIES */

@media screen and (max-width:940px) {

    .sa-iop .sa-iop-content h1 {
        font-size: 2rem;
    }
    
    }

@media screen and (max-width:600px) {
    .sa-iop {
        height: 100vh;
      }
}